import { useLogout } from "../hooks/useLogout"
import { useAuthContext } from "../hooks/useAuthContext";
import icon from '../img/MyComputer.png'
import *  as S from "./nav.style"
export default function Nav() {
    const { logout } = useLogout();
    const { user } = useAuthContext();
    return (
        <S.NavCont>
            <S.LogoCont>
                <S.ComputerIcon src={icon} alt=' '/>
                <S.LogoTxt>
                     
                </S.LogoTxt>
            </S.LogoCont>
            <S.OptUl>
                {!user &&
                <> 
                <li>
                    <S.LoginSignupLink to='/login'>LOGIN</S.LoginSignupLink>
                </li>
                <li>
                    <S.LoginSignupLink to='/signup'>SIGNUP</S.LoginSignupLink>
                </li>
                </>}
                {user &&
                <li>
                    <S.WelcomeMsg>! <strong>{user.displayName}</strong>
                    </S.WelcomeMsg>
                    <S.LogoutBtn type="button" onClick={logout}>
                        LOGOUT
                    </S.LogoutBtn>
                </li>}
            </S.OptUl>
        </S.NavCont>
    )
}
